<template>
      <div id="nav">
      <router-link class="link" :to="`/update/${idx}`"> 회원 수정 </router-link>
      <router-link class="link" :to="`/delete/${idx}`"> 회원 탈퇴 </router-link>
    </div>
</template>

<script> 
  export default { 
    data(){ 
      return { 
        idx: '' ,
       } 
    }, 
    
    created(){   // 컴포넌트 생성하면서 제일 먼저 실행
      // 파라미터(primary key) 전송 받음
      this.idx  = this.$route.params.idx
    } 
  } 
</script>

<style>
.link{
  display: block;
  font-size: 30px;
  text-decoration: none;
  padding: 10px;
}
.link:hover {
  background-color: #cd853f;
  color: white;
}
</style>