<template>
  <div>
    <h2 align="center">로그인</h2>
    <table align="center">
      <tr><td>아이디</td><td><input type="text" v-model="id"/></td></tr>
      <tr><td>비밀번호</td><td><input type="password" v-model="pwd" /></td></tr>
      <tr><td colspan="2"><center><button v-on:click="formSubmit()">
        로그인</button>
      </center></td></tr>
     </table>
  </div>
</template>

<script>
  export default {
    name: 'LoginComp',
    data() {
      return {
        id: '',
        pwd: '',
        idx:''
      };
    },

    methods: {
      formSubmit: function() {  
        if(this.id == '') {
            alert("아이디를 입력해주세요");
            return true;
        }
        if(this.pwd == '') {
            alert("패스워드를 입력해주세요");
            return true;
        }          
        this.$http.post('/api/login', {  
          id: this.id,
          pwd: this.pwd,
        })
        .then((response) => {
          this.output = response.data
          if(this.output == -1) {
            alert('로그인 실패하였어요')             
            this.$router.push({ name: 'index'})
          }
          else {
            alert('로그인 되었어요')  
            this.idx = this.output.idx  // primary key
             // 페이지 이동 및 파라메터(primary key) 전송
            this.$router.push({ name: 'temp', params: {idx: this.idx}})  
          }
        })
        .catch((error) => {
          this.output = error
      })
    }
  }
}
</script>