import { createWebHistory, createRouter } from "vue-router";
import IndexComp from "@/components/views/IndexComp.vue";
import HtmlComp from "@/components/views/HtmlComp.vue";
import CSSComp from "@/components/views/IndexComp.vue";
import JavascriptComp from "@/components/views/JavascriptComp.vue";
import VueJsComp from "@/components/views/VueJsComp.vue";
import NodeJsComp from "@/components/views/NodeJsComp.vue";
import RegistComp from "@/components/member/RegistComp.vue";
import LoginComp from "@/components/member/LoginComp.vue";
import TempComp from "@/components/member/TempComp.vue";
import UpdateComp from "@/components/member/UpdateComp.vue";
import DeleteComp from "@/components/member/DeleteComp.vue";
const routes = [
  {
    path: "/", // Home 컴포넌트 출력
    name: "index",
    component: IndexComp,
  },
  {
    path: "/Html", // about 컴포넌트 출력
    name: "Html",
    component: HtmlComp,
  },
  {
    path: "/CSS", // about 컴포넌트 출력
    name: "CSS",
    component: CSSComp,
  },
  {
    path: "/Javascript", // about 컴포넌트 출력
    name: "Javascript",
    component: JavascriptComp,
  },
  {
    path: "/VueJs", // about 컴포넌트 출력
    name: "VueJs",
    component: VueJsComp,
  },
  {
    path: "/NodeJs", // about 컴포넌트 출력
    name: "NodeJs",
    component: NodeJsComp,
  },
  {
    path: "/Regist", // about 컴포넌트 출력
    name: "Regist",
    component: RegistComp,
  },
  {
    path: "/login", // about 컴포넌트 출력
    name: "login",
    component: LoginComp,
  },
  {
    path: "/temp/:idx", // about 컴포넌트 출력
    name: "temp",
    component: TempComp,
  },
  {
    path: "/update/:idx", // about 컴포넌트 출력
    name: "update",
    component: UpdateComp,
  },
  {
    path: "/delete/:idx", // about 컴포넌트 출력
    name: "delete",
    component: DeleteComp,
  },
];

const router = createRouter({
  // 라우트 생성
  history: createWebHistory(),
  routes,
});
export default router;
