<template>
  <div>
    <h3 align = "center">회원가입</h3>
      <table align="center">
        <tr><td>성    명*</td><td><input type="text" v-model="name"></td></tr>
        <tr><td>아 이 디*</td><td><input type="text" v-model="id"></td></tr>
        <tr><td>패스 워드*</td><td><input type="password" v-model="pwd"></td></tr>
        <tr><td>전화 번호*</td><td><input type="text" v-model="tel"></td></tr>
        <tr><td>직     업</td><td><input type="text" v-model="job"></td></tr>
        <tr><td>성     별</td>
        <td><input type="radio" name= "gender" value= "남자">남자
            <input type="radio" name= "gender" value = "여자" checked>여자
        </td></tr>
        <tr><td>취     미</td>
        <td><input type="checkbox" name= "hobby" value = "음악감상">음악감상
        <input type="checkbox" name= "hobby" value = "운동">운동
        <input type="checkbox" name= "hobby" value = "기타">기타
        </td></tr>

        <tr><td colspan="2"><center><button v-on:click="formSubmit()">회원가입</button>
        </center></td></tr>
      </table>
  </div>  
</template>

<script>
  export default {
    name: 'RegistComp',
    data() {                  // 현재 페이지에서 사용할 데이터
      return {      
        name: '',
        id: '',
        pwd: '',
        tel: '',
        job: '',   
        hobby: '',
        gender: '',
        response: ''
      };
    },

    methods: {              // 메서드 들
      formSubmit: function() {       // 버튼 클릭
        if(this.name == '') {
            alert("이름을 입력해주세요");
            return true;
        }
        if(this.id == '') {
            alert("아이디를 입력해주세요");
            return true;
        }
        if(this.pwd == '') {
            alert("패스워드를 입력해주세요");
            return true;
        }        
        this.$http.post('/api/register', {        // Post 전송
          name: this.name,    // 데이터 전송하여 -> 데이터베이스에 저징
          id: this.id,
          pwd: this.pwd,
          tel: this.tel,
          job: this.job,   
          gender: this.gender,
          hobby: this.hobby,
        })
        .then((response) => {     // 서버의 응답(회원정보) - 사용 안함
          this.response = response.data
          alert(this.name + '님 회원가입되었어요')  // 스크립트
          this.$router.push({ path: '/login' })        // 자동 라우팅(이동)
        })
      }
    }
  }
</script>

<style scoped>
table {
  border-collapse: collapse;
  width: 50%;
  align-content: center;
}
td {
  padding: 0px;
}
tr:hover { background-color: #b8aacf; }

</style>