<template>
  <div>
    <h3 align = "center">회원수정</h3>
    <table align="center">
      <tr><td>성    명</td><td><input type="text" v-model="name"></td></tr>
      <tr><td>아 이 디</td><td><input type="text" v-model="id"></td></tr>
      <tr><td>패스 워드</td><td><input type="password" v-model="pwd"></td></tr>
      <tr><td>전화 번호</td><td><input type="text" v-model="tel"></td></tr>
      <tr><td>직     업</td><td><input type="text" v-model="job"></td></tr>
      <tr><td>성     별</td>
        <td><input type="radio" v-model="gender">남자
            <input type="radio" v-model="gender" checked>여자
      </td></tr>
      <tr><td>취     미</td>
        <td><input type="checkbox">음악감상
            <input type="checkbox">운동
            <input type="checkbox">기타
      </td></tr>

      <tr><td colspan="2"><center><button v-on:click="formSubmit()">회원수정</button>
      </center></td></tr>
    </table>
  </div>  
</template>

<script>
export default {
  name: 'UpdateComp',
  data() {
    return {
      name: '',
      id: '',
      pwd: '',
      tel: '',
      job: '',   
      hobby: false,
      gender: false,
      idx: '',   
      };
   },
  created () {            // 컴포넌트가 생성될 때 실행
    this.idx  = this.$route.params.idx,      // 파라미터(primary key) 받음                      
    this.$http.post(`/api/data/${this.idx}`)     // post 전송  
    .then((response) => {               // 정보를 가져와서 뿌려줌
      this.data = response.data;  
      this.name = this.data.name;
      this.id = this.data.id;
      this.pwd = this.data.pwd;
      this.tel = this.data.tel;          
      this.job = this.data.job;
      this.gender = this.data.gender;              
      this.hobby = this.data.hobby;                                        
    })
  },

  methods: {
    formSubmit: function() {
      this.$http.post(`/api/edit/${this.idx}`, {  
        name: this.name,
        id: this.id,
        pwd: this.pwd,
        tel: this.tel,
        job: this.job,   
        gender: this.gender,
        hobby: this.hobby,
      })
      .then((response) => {
        this.output = response.data
        this.$router.push({ name: 'temp', params: {idx: this.idx}})
      })
      .catch((error) => {
        this.output = error
      })
    }
  }
}
</script>