<template>
    <div>
      <h2> 회원탈퇴 </h2>
      <h3> {{name}} 님 탈퇴하겠습니까 </h3>
      <ul>
       <button @click="formSubmit()">회원탈퇴</button><br>
        <router-link :to="`/temp/${idx}`">아니오</router-link>  
      </ul>    
      {{idx}}
    </div>
</template>

<script>
  export default {
    name: 'DeleteComp',
    data(){ 
        return { 
          name: '',      
          idx: '' ,
        } 
      }, 

    created(){ 
      this.idx  = this.$route.params.idx,
      this.$http.post(`/api/data/${this.idx}`)     
      .then((response) => {                    // 이름만 가져옴
        this.name = response.data.name;   
      });           
    },

    methods: {
      formSubmit () {    
        this.$http.post(`/api/delete/${this.idx}`)
        .then((response) => {
           this.output = response.data
           alert(this.name + '님 회원 탈퇴 되었습니다')
           this.$router.push({ name: 'login'})
        })
        .catch((error) => {
          this.output = error
        })
      }
    }
  }
</script>

